// 2Ô∏è‚É£ ActionsÏóêÏÑú commitÏùÑÌï¥ÏÑú MutationsÏóê Í∞íÏùÑ ÎÑòÍ∏∞Í≥† ÏûàÎã§.
// ActionsÏùò Ï£ºÏöî Î™©Ï†ÅÏùÄ mutationsÎ•º Ïã§ÌñâÏãúÌÇ§Îäî Í≤É
// commit('Ìï®ÏàòÎ™Ö', 'Ï†ÑÎã¨Ïù∏Ïûê')ÏúºÎ°ú Mutations Ìò∏Ï∂úÌïúÎã§.

import {
  //apiÎì§Í≥†Ïò¥
  fetchNewsList,
  fetchJobsList,
  fetchAskList,
  fetchUserInfo,
  fetchCommentItem,
  fetchList,
} from "../api/index.js";

export default {
  // üëápromise ÎπÑÎèôÍ∏∞ Ï≤òÎ¶¨ Ìå®ÌÑ¥
  // FETCH_NEWS(context) {
  //   return fetchNewsList()
  //     .then((response) => {
  //       // console.log(response.data);
  //       context.commit("SET_NEWS", response.data);
  //     })
  //     .catch((error) => {
  //       console.log(error);
  //     });
  // },

  // üßê async&await Íµ¨Î¨∏
  // --> ÎπÑÎèôÍ∏∞Ï≤òÎ¶¨ÏΩîÎìúÏóêÏÑú return ÏùÑ ÏûëÏÑ±ÌïòÏßÄ ÏïäÏúºÎ©¥ ÏÉùÍ∏∞Îäî ÏùºÏùÄ?
  // ÌôîÎ©¥ÏóêÏÑú disPatchÌïòÍ≥† ÎÇòÏÑú (promise)thenÏúºÎ°ú Ï≤¥Ïù¥ÎãùÏùÑ Ìï¥ÏïºÌïòÎäîÎç∞ Î¶¨ÌÑ¥Ïù¥ ÏïàÎÑòÏñ¥Ïò§Î©¥ ÏùòÎèÑÌïú ÎåÄÎ°ú ÎπÑÎèôÍ∏∞ Ï≤òÎ¶¨Í∞Ä ÎêòÏßÄ ÏïäÎäîÎã§.
  // ÌôîÎ©¥ÏóêÏÑúÏùò ÎπÑÎèôÍ∏∞ÏàúÏÑúÎ•º Î≥¥Ïû•Ìï† Ïàò ÏóÜÍ∏∞ ÎïåÎ¨∏Ïóê(UXÍ∞Ä Î∂àÏïàÏ†ïÌï¥Î≥¥Ïù¥Í≥† ÏïΩÍ∞ÑÏùò Ïò§Î•ò), Í≤∞Í≥ºÍ∞íÏùÑ Ìï≠ÏÉÅ Î∞òÌôòÌï¥Ï§òÏïº ÌïúÎã§.
  async FETCH_NEWS(context) {
    const response = await fetchNewsList();
    context.commit("SET_NEWS", response.data);
    return response;
  },

  //ES6 Destructuring Î¨∏Î≤ïÏúºÎ°ú {commit}, {data}Í∞Ä Í∞ÄÎä•ÌïòÍ≤å ÎêúÎã§.
  FETCH_JOBS({ commit }) {
    return fetchJobsList()
      .then(({ data }) => {
        // console.log(commit);
        commit("SET_JOBS", data);
      })
      .catch((error) => console.log(error));
  },

  async FETCH_ASK({ commit }) {
    try {
      const response = await fetchAskList();
      commit("SET_ASK", response.data);
      return response;
    } catch (error) {
      console.log(error);
    }
  },

  //‚ôªÔ∏è ÏúÑÏùò news,ask,jobsÎ•º ListÎ°ú Ìïú Î≤àÏóê Ï≤òÎ¶¨ÌïòÏó¨ Ï§ëÎ≥µÏÇ¨Ïö©Ìï† Ïàò ÏûàÎã§.
  //try , catchÍµ¨Î¨∏ ÏûëÏÑ±ÏùÄ actionÌè¥Îçî Î≥¥Îã§Îäî api Ìè¥ÎçîÏùò index.jsÎ∂ÄÎ∂ÑÏóêÏÑú ÌïúÎã§. Í∞ÄÎèÖÏÑ±.
  async FETCH_LIST({ commit }, pageName) {
    // ‚ö™2Ô∏è‚É£api Ìò∏Ï∂úÌïòÍ≥†
    const response = await fetchList(pageName);
    // ‚ö™3Ô∏è‚É£mutationÌò∏Ï∂ú
    commit("SET_LIST", response.data);
    return response;
  },

  FETCH_USER({ commit }, name) {
    return fetchUserInfo(name)
      .then(({ data }) => {
        commit("SET_USER", data);
      })
      .catch((error) => console.log(error));
  },

  FETCH_ITEM({ commit }, id) {
    return fetchCommentItem(id)
      .then(({ data }) => {
        commit("SET_ITEM", data);
      })
      .catch((error) => console.log(error));
  },
};
