      <!-- :infoÎ°ú Ï∂ïÏïΩÍ∞ÄÎä• -->
      <user-profile props:info="fetchedItem">
        <!-- :infoÎùºÍ≥† propsÎÑòÍ≤®Ï§ÄÎã§. -->
        <!-- üêõüêõv-slot ÏùÄ templateÏùÑ Í∞ôÏù¥ Ïç®Ï§òÏïºÎßå Í∞ÄÎä•ÌïòÎã§. -->
        <template v-slot:username>
