<script>
//Ìó¨ÌçºÌï®Ïàò Í∫ºÎÇ¥Ïò§Í∏∞
//vuex bindingsÏóê fetchedItemÏúºÎ°ú ÎêòÏñ¥ÏûàÏùå.
import { mapGetters } from "vuex";
import UserProfile from "@/components/UserProfile.vue";

export default {
  components: {
    UserProfile,
  },
  computed: {
    // storeÏùò gettersÏùò fetchedAskÎùºÎäî Î≥ÄÏàòÎ•º Î∞îÎ°ú Ïì∞Í≥†Ïã∂Îã§Î©¥, storeÏóê Ï†ïÏùòÎêú ÎåÄÎ°ú Í∞ùÏ≤¥Í∞Ä ÏïÑÎãàÎùº Î∞∞Ïó¥Î°ú Ïó∞Í≤∞ÌïòÎ©¥ ÎêúÎã§.
    ...mapGetters(["fetchedItem"]),
  },
  created() {
    //ASKÏùò listÏóêÏÑú ÌÅ¥Î¶≠Ìï¥ÏÑú Ïù¥ÎèôÌïòÎ©∞ Ìï¥Îãπ Í∞íÏùÑ ÎÑ£Ïñ¥Ï£ºÏóàÏùå. -> this.$route.params.idÏÇ¨Ïö©Í∞ÄÎä•
    const itemId = this.$route.params.id;
