// node_modules/axios ÎÖ∏ÎìúÎ™®ÎìàÏóê ÏÑ§ÏπòÎêú ÎùºÏù¥Î∏åÎü¨Î¶¨ axiosÎ•º Îì§Í≥†Ïò®Îã§.
// üí°axiosÎäî promiseÍ∏∞Î∞òÏúºÎ°ú apiÎ•º Ï†úÍ≥µÌïúÎã§.
import axios from "axios";

// 1. HTTP Request & ResponseÏôÄ Í¥ÄÎ†®Îêú Í∏∞Î≥∏ ÏÑ§Ï†ï (ÌÜ†ÌÅ∞Ï≤òÎ¶¨ÌïúÎã§Í≥† ÌïòÎ©¥ ÌÜ†ÌÅ∞Í¥ÄÎ†®Îêú Î°úÏßÅÎì§ÎèÑ Í≥µÌÜµÏ†ÅÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏùå)
//Ï£ºÏÜåÍ∞Ä Îí∑ Î∂ÄÎ∂ÑÏù¥ Îã¨ÎùºÏßÄÍ≥† ÏïûÎ∂ÄÎ∂ÑÏùÄ ÎèôÏùºÌï† Îïå, baseUrlÎ°ú Î¨∂Ïñ¥ÏÑú.
const config = {
  baseUrl: "https://api.hnpwa.com/v0/",
};

// 2. API Ìï®ÏàòÎì§ÏùÑ Ï†ïÎ¶¨
// baseUrl Îã§Ïùå +Î°ú Î≥ÄÏàòÎ•º Ïó∞Í≤∞, Îí§Ïùò ÎÇòÎ®∏ÏßÄ Ï£ºÏÜåÎ•º Î∂ôÏó¨ÏÑú Ï≤òÎ¶¨ÌïúÎã§.
// ES6 Î¨∏Î≤ï: ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î≥ÄÏàòÏôÄ Î¨∏ÏûêÎ•º Ïó∞Í≤∞ÌïòÎäî Í≤ÉÏùÑ ``Î∞±Ìã±Í≥º $Îã¨Îü¨ Ïì∞Î©∞ Î∞îÍøÄ Ïàò ÏûàÎã§.
// try, catchÎäî ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏Ïùò Ï†ÑÌÜµÏ†ÅÏù∏ ÏïÑÏ£º Ïò§ÎûòÎêú ÏòàÏô∏Ï≤òÎ¶¨ Íµ¨Î¨∏Ïù¥Îã§.

async function fetchNewsList() {
  try {
    //axios.get(config.baseUrl + "news/1.json");
    const response = await axios.get(`${config.baseUrl}news/1.json`);
    return response;
  } catch (error) {
    console.log(error);
  }
}

async function fetchAskList() {
  try {
    const response = await axios.get(`${config.baseUrl}ask/1.json`);
    return response;
  } catch (error) {
    console.log(error);
  }
}

async function fetchJobsList() {
  try {
    //LintÏóêÏÑú Ïù¥Î†áÍ≤å Ïì∞Í∏∞ÎèÑ..
    return await axios.get(`${config.baseUrl}jobs/1.json`);
  } catch (error) {
    console.log(error);
  }
}
//‚ôªÔ∏è ÏúÑÏùò news,ask,jobsÎ•º ListÎ°ú Ìïú Î≤àÏóê Ï≤òÎ¶¨ÌïòÏó¨ Ï§ëÎ≥µÏÇ¨Ïö©Ìï† Ïàò ÏûàÎã§.
async function fetchList(pageName) {
  try {
    return await axios.get(`${config.baseUrl}${pageName}/1.json`);
  } catch (error) {
    console.log(error);
  }
}

async function fetchUserInfo(username) {
  try {
    return await axios.get(`${config.baseUrl}user/${username}.json`);
  } catch (error) {
    console.log(error);
  }
}

async function fetchCommentItem(id) {
  try {
    return await axios.get(`${config.baseUrl}item/${id}.json`);
  } catch (error) {
    console.log(error);
  }
}

export {
  fetchNewsList,
  fetchAskList,
  fetchJobsList,
  fetchList,
  fetchUserInfo,
  fetchCommentItem,
};

